@import url("https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Baloo Tammudu 2", sans-serif;
}

.main_div {
  display: flex;
  justify-content: center;
  min-height: 100vh;
  background: linear-gradient(
    45deg,
    rgba(60, 74, 84, 1) 0%,
    rgba(0, 139, 255, 1) 25%,
    rgba(245, 255, 0, 1) 50%,
    rgba(255, 0, 0, 1) 75%,
    rgba(235, 0, 255, 1) 95%
  );
  background-size: cover;
  animation: bg 15s ease infinite;
}
@keyframes bg {
  0%,
  100% {
    filter: hue-rotate(0);
  }
  50% {
    filter: hue-rotate(360deg);
  }
}

.videos,
.posts {
  margin: 2rem 0;
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  grid-gap: 2rem;
}

.videos .recent_uploads,
.recent_posts {
  text-transform: capitalize;
  border-bottom: 1px solid rgb(190, 190, 190);
}
.video,
.post {
  display: flex;
  align-items: center;
}

.video .thumb_img {
  width: 100%;
  height: 100%;
  position: relative;
}
.post .thumb_img {
  width: 60%;
  height: 100%;
  position: relative;
}

.video img,
.post img {
  width: 100%;
  height: 100%;
}

.thumb_img .content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: 0.4s ease;
}

.thumb_img .content a {
  padding: 0.5rem;
  display: block;
  font-size: 2.5rem;
  color: #000;
  transform: translateY(2rem);
  transition: transform 0.25s ease;
}

.video .thumb_img:hover .content,
.post .thumb_img:hover .content {
  opacity: 1;
  backdrop-filter: blur(2px);
}

.video .thumb_img:hover .content a,
.post .thumb_img:hover .content a {
  transform: translateY(0);
}

.details {
  padding: 0 1rem;
}
.desc {
  border-bottom: 1px solid rgb(190, 190, 190);
  padding-bottom: 0.5rem;
}

.video p:nth-child(2),
.video p:nth-child(3),
.video p:nth-child(4),
.video p:nth-child(5) {
  color: #948c81;
}

footer {
  background: #f1f1f1;
}
footer p {
  padding: 1rem;
  margin: 0;
}

.header {
  height: 75vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.header h1,
.header p {
  animation: header 1s ease forwards;
}

.header h1 {
  position: relative;
}

.header h1::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 1px;
  background: #111;
  animation: line 1s ease 1.5s forwards;
}

@keyframes line {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

@keyframes header {
  0% {
    transform: translateY(5rem) scale(0.6);
  }
  100% {
    transform: translateY(0) scale(1);
  }
}

.about_head {
  text-align: center;
  border-bottom: 1px solid rgb(190, 190, 190);
}

@media (max-width: 768px) {
  .video,
  .post {
    flex-direction: column;
  }
  .video .thumb_img,
  .post .thumb_img {
    width: 100%;
  }

  .video h2,
  .post h2 {
    margin-top: 1rem;
  }
  .header {
    height: 87vh;
  }
}
